/* Single Page Layout */
body {
  color: white;
  background-color: rgb(20, 20, 20);
  margin: 0;
  padding: 0;
}

* {
  font-family: "Garamond";
  box-sizing: border-box;
}

a {
  color: white;
  text-decoration-thickness: 0.1px;
  text-underline-offset: 5px;
}

.single-page-container {
  color: rgb(205, 205, 205);
  font-size: 18px;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

/* Header */
.page-header {
  text-align: center;
  padding: 60px 0 40px;
}

.main-title {
  font-size: 48px;
  font-weight: normal;
  color: white;
  margin: 0 0 10px;
  letter-spacing: 2px;
}

.subtitle {
  font-size: 16px;
  color: rgb(150, 150, 150);
  margin: 0;
}

/* Navbar styling */
.navbar-container {
  position: sticky;
  top: 0;
  background-color: rgb(20, 20, 20);
  padding: 15px 0;
  z-index: 100;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 30px;
}

.navbar {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.nav-link {
  color: gray;
  text-decoration-thickness: 0.1px;
  text-underline-offset: 5px;
  transition: color 0.2s;
}

.nav-link:hover {
  color: white;
}

/* Section styling */
.page-section {
  margin-bottom: 80px;
  padding-top: 20px;
}

.section-title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  color: white;
}

/* About section */
.about-page {
  line-height: 1.6;
}

.idea-container {
  margin-left: 25px;
  margin-right: 25px;
}

/* Coding section */
.codePage h2 {
  font-size: 20px;
  margin-top: 30px;
  margin-bottom: 10px;
}

.code-img {
  width: min(100%, 450px);
  border-radius: 8px;
  margin-top: 10px;
  margin-bottom: 10px;
  display: block;
}

/* Art section */
.artPage {
  line-height: 1.6;
}

.art-section-container {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.landscape-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.landscape-image {
  width: min(100%, 400px);
  border-radius: 8px;
  margin-bottom: 8px;
}

.portrait-pair-container {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.portrait-container {
  flex: 1;
  min-width: 150px;
  max-width: 200px;
}

.portrait-image {
  width: 100%;
  border-radius: 8px;
  margin-bottom: 8px;
}

.art-description {
  font-size: 14px;
  margin: 4px 0;
  color: rgb(180, 180, 180);
}

.art-date {
  font-size: 12px;
  margin: 0;
  color: rgb(140, 140, 140);
}

/* Writing section */
.writingPage {
  line-height: 1.8;
}

/* Responsive adjustments */
@media (max-width: 600px) {
  .single-page-container {
    padding: 15px;
    font-size: 16px;
  }
  
  .code-img,
  .landscape-image {
    width: 100%;
    max-width: 100%;
  }
  
  .portrait-container {
    max-width: 45%;
  }
  
  .section-title {
    font-size: 24px;
  }
}
